.form {
  display: flex;
  flex-wrap: wrap;

  padding: 0 10px;


  & input[type=text],
  & input[type=tel],
  & input[type=email],
  & textarea {
    box-sizing: border-box;
    width: 100%;

    // padding: 14px 18px 16px 0;
    padding: 4px 0 15px;
    color: $dark-color;
    border: none;
    border-bottom: 1px solid rgba(225, 225, 225, .8);
    border-radius: 0;

    color: $dark-color;
    font-size: 20px;
    line-height: 2.5;
    letter-spacing: 0.8px;

    background-color: transparent;

    & input[type=tel] {
      padding-bottom: 17px; // Ради PP!!!
    }

    &::placeholder {
      color: inherit;
      // font-size: inherit;
      // line-height: inherit;
      // letter-spacing: inherit;
      background-color: transparent;
      opacity: 1;
    }

    &:hover,
    &:focus {
      border-bottom-color: rgba(225, 225, 225, 1);
    }
  }

  & textarea {
    resize: none;
    height: 72px;
  }

  & button {
    @include button;

    min-width: 230px;
    max-width: 350px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 33px;
    margin-bottom: 20px;
    padding: 3px 4px 7px 8px;

    font-size: 25px;
    font-weight: 700;
    font-family: 'Raleway', Helvetica, sans-serif;
    line-height: 2;
    letter-spacing: 1px;
    color: $light-color;
    text-transform: none;


    border: none;
    background-color: $dark-color;
    box-shadow: 0px 3px 0 0 rgba(0, 0, 0, 0.3);

    &:hover,
    &:focus {
      color: $dark-color;
    }
  }
}

.form__field {
  flex-grow: 1;
  flex-shrink: 1;

  margin-right: 32px;
  margin-bottom: 22px;

    &--text,
    &--tel {
      flex-basis: 34%;
  }

  &--tel {
    margin-left: 10px;
  }

  &--email,
  &--textarea {
    flex-basis: 100%;
  }

  &--textarea {
    margin-top: 7px;
    margin-bottom: 12px;
  }
}

.form__conditions {
  flex-basis: 100%;

  font-size: 12px;
  line-height: 1.5;
  // letter-spacing: normal;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
}

.form__conditions p {
  margin-bottom: 0;
}

.form__conditions a {
  @include dotted-line(rgba(0, 0, 0, 0.5));
  position: relative;

  display: inline-block;
  padding-bottom: 4px;

  &:hover,
  &:focus {
    @include dotted-line($dark-color);
    color: $dark-color;
    text-decoration: none;
  }
}


@media (max-width: $m-width-max) {
  .form {
    padding-right: 22px;
    padding-left: 24px;

    & input[type=text],
    & input[type=tel],
    & input[type=email],
    & textarea {
      font-size: 14px;
      line-height: 2.54;
      letter-spacing: 0.56px;
    }

    & textarea {
      height: 55px;
      margin-bottom: -4px;
    }

    & button {
      min-width: 164px;
      max-width: 250px;

      font-size: 15.5px;
      line-height: 2.29;
      letter-spacing: 0.62px;
    }
  }

  .form__field {
    margin-bottom: 14px;
    margin-left: 21px;
    margin-right: 21px;

    &--text {
      flex-basis: 20%;
    }

    &--tel {
      flex-basis: 30%;
    }

    &--textarea {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  .form__conditions {
    margin-top: 0;

    font-size: 10px;
    line-height: 1.3;
  }

  .form__conditions a {
    padding-bottom: 0;
  }

  .form button {
    min-width: 164px;
    margin-top: 28px;
    margin-bottom: 16px;
    padding-top: 1px;

    box-shadow: none;
  }
}

@media (max-width: $s-width-max) {
  .form {
    padding-left: 20px;
    padding-right: 20px;

    & input[type=text],
    & input[type=tel],
    & input[type=email],
    & textarea {
      padding-bottom: 13px;
    }
  }

  .form__field {
    margin-right: 0;
    margin-left: 0;


    &--text {
      margin-right: 43px;
      flex-basis: 40%;
    }

    &--tel {
      flex-basis: 40%;
    }
  }

  .form__conditions {
    margin-top: 12px;
  }

  .form button {
    margin-top: 32px;
    margin-bottom: 38px;
  }
}
